.trailers {
  padding: 30px 0 0 30px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
    
  > section {
    display: flex;
    overflow-x: auto;
    padding-top: 20px;
    padding-bottom: 40px;

    &::-webkit-scrollbar {
      height: 8px;
    }
  
    &::-webkit-scrollbar-thumb {
      background-color: #dbdbdb;
      border-radius: 8px;
    }

    > div {
      text-align: center;
      width: 300px;
      margin: 0 10px;
      flex-shrink: 0;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      > button {
        border: none;
        background: none;
        width: 100%;
        height: 168px;
        position: relative;
        transition: transform 0.2s ease-in-out;
        cursor: pointer;

        &:hover {
          transform: scale(1.06);

          > img:last-child {
            transform: scale(1.3);
          }
        }

        > img:first-child {
          width: 100%;
          height: 100%;
          border-radius: 8px;
          object-fit: cover;
        }

        > img:last-child {
          width: 160px;
          height: 160px;
          object-fit: contain;
          position: absolute;
          top: calc(50% - 80px);
          left: calc(50% - 80px);
          transition: transform 0.2s ease-in-out;
        }
      }

      > a {
        > h2 {
          color: white;
          font-size: 19px;
          font-weight: 600;
          margin-top: 10px;
          cursor: pointer;
        }
      }

      > h3 {
        color: white;
        font-size: 16px;
        font-weight: 400;
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .trailers {
    padding: 30px 20px 0 20px;
  }
}