.modal {
  display: flex;
  justify-content: center;
  align-items: center;
}

.trailer {
  background-color: #282828;
  height: 80%;
  width: 75%;

  > div {
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 64px;
    padding: 0 20px;

    > h2 {
      font-size: 20px;
      color: white;
      font-weight: 400;
    }

    > button {
      border: none;
      background: none;
      border-radius: 6px;
      cursor: pointer;
      padding: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      transition-duration: 0.3s;

      &:hover {
        background-color: white;
      }

      > img {
        width: 28px;
        height: 28px;
        object-fit: contain;
      }
    }
  }

  > iframe {
    width: 100%;
    height: 100%;
    border: none;
  }
}

@media only screen and (max-width: 500px) {
  .trailer {
    width: 85%;
  }
}