.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 30px;

  > button {
    margin: 5px 4px 0 4px;
    font-weight: 600;
    background: none;
    border: none;
    font-size: 16px;
    cursor: pointer;

    &.curr-page {
      padding: 2px 8px;
      cursor: auto;
      font-weight: 400;
      border: 1px solid black;
    }

    &.ellipsis {
      letter-spacing: 1px;
      cursor: auto;
      margin: 0;
    }
  }

  > :first-child {
    margin-left: 0;
  }

  > :last-child {
    margin-right: 0;
  }
}