.search-data-display {
  > .film {
    display: flex;
    align-items: center;
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid rgba(227, 227, 227, 1);
    box-shadow: 0 2px 8px rgb(0 0 0 / 10%);
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    > a {
      > img {
        width: 94px;
        height: 141px;
        object-fit: cover;
      }

      > .no-image {
        width: 94px;
        height: 141px;
        background-color: #DBDBDB;
        display: flex;
        justify-content: center;
        align-items: center;

        > img {
          width: 48px;
          height: 48px;
          object-fit: cover;
        }
      }
    }

    > div {
      padding: 10px 15px;
      overflow: hidden;

      > a {
        text-decoration: none;
        color: black;

        > h2 {
          display: inline;
          font-size: 19px;
          font-weight: 600;

          &:hover {
            color: #666;
          }
        }
      }

      > p:nth-of-type(1) {
        color: #999;
      }

      > .overview {
        margin-top: 20px;
        color: black !important;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  > .person {
    display: flex;
    align-items: center;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

    > a {
      margin-right: 20px;

      > img {
        width: 70px;
        height: 70px;
        object-fit: cover;
        border-radius: 8px;
      }

      > .no-image {
        width: 70px;
        height: 70px;
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #DBDBDB;

        > img {
          width: 36px;
          height: 36px;
          object-fit: cover;
        }
      }
    }

    > div {
      > a {
        text-decoration: none;
        color: black;

        > h2 {
          font-size: 20px;
          font-weight: 600;
        }
      }

      > p {
        > span {
          &:after {
            content: "";
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: black;
            margin-left: 4px;
            margin-bottom: 2px;
            display: inline-block;
          }
        }

        > a {
          text-decoration: none;
          color: black;
          margin-left: 4px;

          &:nth-of-type(1) {
            margin-left: 5px;
          }
        }
      }
    }
  }
}