.facts {
  padding: 30px 0;
  border-bottom: 1px solid #d7d7d7;
    
  > .link-container {
    display: flex;
    align-items: center;
    margin-bottom: 30px;

    > div {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      margin: 0 5px;
      cursor: pointer;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      > a {
        display: flex;
        justify-content: center;
        align-items: center;

        > img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      &:hover {
        > span {
          visibility: visible;
        }
      }

      > span {
        visibility: hidden;
        transition: visibility 50ms ease-in-out;
        width: 120px;
        background-color: rgba(3, 37, 65, 1);
        color: #fff;
        text-align: center;
        border-radius: 4px;
        padding: 5px 0;
        position: absolute;
        bottom: 120%;
        left: 50%;
        margin-left: -60px;
        cursor: auto;

        &:after {
          content: "";
          position: absolute;
          top: 100%;
          left: 50%;
          margin-left: -5px;
          border-width: 5px;
          border-style: solid;
          border-color: black transparent transparent transparent;
        }
      }
    }

    > .homepage {
      width: 39px;
      height: 30px;
      border-left: 1px solid #d7d7d7;
      padding-left: 6px;
    }
  }

  > h3 {
    font-size: 18px;
    font-weight: 600;
  }

  > h4 {
    font-weight: 600;
    font-size: 16px;
    margin-top: 20px;
  }

  > .networks {
    > img {
      display: block;
      margin-bottom: 8px;
      height: 30px;
      object-fit: contain;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  > .keywords {
    margin-top: 30px;
    margin-bottom: 10px;
  }

  > .keywords-container {
    display: flex;
    flex-wrap: wrap;

    > span {
      font-size: 14px;
      background-color: rgba(0,0,0,0.1);
      border: 1px solid #d7d7d7;
      padding: 4px 10px;
      border-radius: 4px;
      margin: 0 5px 10px 0;
    }
  }
}