.curr-season {
  padding: 30px 0;
  border-bottom: 1px solid #d7d7d7;

  > h2 {
    font-size: 22px;
    font-weight: 600;
  }

  > section {
    margin: 20px 0;
    display: flex;
    border: 1px solid rgb(227, 227, 227);
    box-shadow: 0 2px 8px rgb(0 0 0 / 10%);
    border-radius: 8px;
    overflow: hidden;

    > .poster {
      width: 130px;
      border-right: 1px solid rgba(227, 227, 227, 1);
      flex-shrink: 0;

      > img {
        width: 100%;
        height: 195px;
        object-fit: cover;
      }

      > div {
        background-color: #DBDBDB;
        width: 100%;
        height: 195px;
        display: flex;
        justify-content: center;
        align-items: center;

        > img {
          width: 64px;
          height: 64px;
          object-fit: cover;
        }
      }
    }

    > .content {
      padding: 20px;
      display: flex;
      flex-direction: column;

      > h2 {
        font-weight: 600;
      }

      > h4 {
        display: flex;
        align-items: center;
        font-weight: 600;

        > div {
          display: flex;
          align-items: center;
          background-color: black;
          padding: 3px 10px;
          border-radius: 8px;
          margin-right: 8px;

          > img {
            width: 14px;
            height: 14px;
            object-fit: cover;
            filter: invert(1);
            margin-right: 2px;
          }

          > span {
            font-size: 15px;
            color: white;
          }
        }
      }

      > p {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 20px;

        > img {
          width: 22px;
          height: 22px;
          object-fit: cover;
          margin-right: 4px;
        }

        > span {
          border-bottom: 1px solid black;
          margin-right: 6px;
        }
      }
    }
  }

  > p {
    font-size: 18px;
    font-weight: 600;
  }
}

@media only screen and (max-width: 500px) {
  .curr-season {
    > section {
      flex-direction: column;

      > .poster {
        border: none;
      }
    }
  }
}