.collection {
  padding: 30px 0;
  border-bottom: 1px solid #d7d7d7;

  > div {
    padding: 80px 20px;
    background-size: cover;
    background-position: 70% 50%;
    background-repeat: no-repeat;
    border-radius: 8px;

    > h2 {
      font-size: 30px;
      font-weight: 600;
      color: white;
    }
  
    > p {
      font-size: 18px;
      color: white;
      margin-bottom: 20px;
    }
  
    > button {
      background-color: rgba(52.5, 31.5, 10.5, 1);
      padding: 9px 22px;
      border-radius: 20px;
      border: none;
      font-size: 18px;
      color: white;
      font-weight: 700;
    }
  }
}